@import "../../../node_modules/bulma/bulma.sass";

@import "../../../node_modules/line-awesome/dist/line-awesome/css/line-awesome.min.css";

:root {
  --background-color: #ededed;
  --box-shadow-bottom-corner: #a6a6a6;
  --box-shadow-top-corner: rgba(255, 255, 255, 0.75);
  --primary-color: #2667ff;
  --text-color: #707070;
  --text-muted-color: #ccc;
  --text-muted-hover-color: #d6d6d6;
}

#app {
  background-color: var(--background-color);
  height: 100vh;
  font-weight: bold;
  color: var(--text-color);
}

.box {
  color: var(--text-color);
}

.title,
.subtitle {
  color: var(--text-color);
}

@mixin concave($offset, $radius) {
  border-radius: #{$radius}px;
  background: linear-gradient(145deg, #d5d5d5, #fefefe);
  box-shadow: #{$offset}px #{$offset}px #{$offset * 3}px #c9c9c9,
    -#{$offset}px -#{$offset}px #{$offset * 3}px #ffffff;
}

@mixin outset($offset, $radius) {
  background: var(--background-color);
  border-radius: #{$radius}px;
  box-shadow: #{$offset}px #{$offset}px #{$offset * 2}px
      var(--box-shadow-bottom-corner),
    -#{$offset}px -#{$offset}px #{$offset * 2}px var(--box-shadow-top-corner);
}

@mixin inset($offset, $radius) {
  background: var(--background-color);
  border-radius: #{$radius}px;
  box-shadow: inset #{$offset}px #{$offset}px #{$offset * 2}px
      var(--box-shadow-bottom-corner),
    inset -#{$offset}px -#{$offset}px #{$offset * 2}px
      var(--box-shadow-top-corner);
}

%gradient {
  background: rgb(93, 46, 140);
  background: linear-gradient(
    135deg,
    rgba(93, 46, 140, 1) 0%,
    rgba(38, 103, 255, 1) 100%
  );
}

input.input,
input.input:focus {
  @include inset(3, 999);
  padding: 0.5rem 1.5rem;
  height: 3rem;
  font-size: 1rem;
  color: var(--text-color);
  font-weight: bold;
  border: none;
}

.authentication {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.sign-in {
  @include outset(5, 20);
  padding: 40px 60px;
  max-width: 800px;
  min-width: 600px;
}

@mixin insetWithShadow($offset) {
  box-shadow: inset #{$offset}px #{$offset}px #{$offset * 2}px
      rgba(30, 30, 30, 0.3),
    -#{$offset}px -#{$offset}px #{$offset * 2}px rgba(30, 30, 30, 0.15);
}

.button.is-primary {
  border-radius: 999px;
  box-shadow: 5px 5px 10px rgba(38, 103, 255, 0.3),
    -5px -5px 10px rgba(93, 46, 140, 0.15);
  width: 100%;
  height: 3rem;
  font-weight: bold;
  letter-spacing: 1px;
  @extend %gradient;
  &:hover {
    @include insetWithShadow(5);
  }
}

.button {
  transition: 0.3s ease;
  &:hover {
    transform: translateY(1px);
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 4rem;
  height: 2rem;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  @include inset(1, 999);
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 999px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 2rem;
  width: 2rem;
  border-radius: 999px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  background: linear-gradient(145deg, #d5d5d5, #fefefe);
  $offset: 2;
  box-shadow: 2px 2px 4px
      var(--box-shadow-bottom-corner);
}

input:checked + .slider {
  @extend %gradient;
  box-shadow: inset 5px 5px 10px rgba(60, 60, 60, 0.3),
    -5px -5px 10px rgba(60, 60, 60, 0.15);
}

input:focus + .slider {
}

input:checked + .slider:before {
  -webkit-transform: translateX(2rem);
  -ms-transform: translateX(2rem);
  transform: translateX(2rem);
}

.is-vertical-aligned {
  display: flex;
  align-items: center;
}

.is-horizontal-aligned {
  display: flex;
  justify-content: center;
}

.button {
  &.is-facebook,
  &.is-google {
    width: 3rem;
    height: 3rem;
    padding: 0;
    position: relative;
    border: none;
    color: #fff;
    border-radius: 999px;
    & > i {
      position: absolute;
      font-size: 1.5rem;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &:hover {
      @include insetWithShadow(3);
    }
  }
  &.is-facebook {
    background: linear-gradient(
      160deg,
      rgba(63, 147, 214, 1) 0%,
      rgba(44, 94, 219, 1) 100%
    );

    box-shadow: 5px 5px 10px rgba(44, 94, 219, 0.3) 100%,
      inset -5px -5px 10px rgba(63, 147, 214, 0.15) 0%;
  }
  &.is-google {
    background: linear-gradient(
      160deg,
      rgba(210, 57, 44, 1) 0%,
      rgba(234, 67, 53, 1) 100%
    );
    box-shadow: 5px 5px 10px rgba(234, 67, 53, 0.3) 100%,
      inset -5px -5px 10px rgba(210, 57, 44, 0.15) 0%;
  }
}

a.link {
  color: var(--text-muted-color);
  &:hover {
    color: var(--text-muted-hover-color);
  }
}
